// _base.scss

@import "../_variables.scss";

*, 
*::after, 
*::before {
    box-sizing: border-box;
}

html, 
body {
	margin: 0;
	padding: 0;
	height: 100%;
	font-family: $font-stack;
	color: $body-color;
	text-align: left;
	background: $bg-body-color;
	 -webkit-animation: bugfix infinite 1s;
}
body {
	font-size: 1rem;
}
body, 
form {
	margin: 0;
	padding: 0;
}