/* Variables */

// Colorset / Default
$interhome-red: #e63957;								// rgba(230, 57, 87, 1)
$interhome-blue: #0096DB;								// rgba(0, 150, 219, 1)
$interhome-grey: #e5e6e8;								// rgba(229, 230, 232, 1)

// Colorset / Calendar
$interhome-cal-available: #D7F6E2;						// helles Grün
$interhome-cal-selected: $interhome-red;				// Interhome Rot
$interhome-cal-first-date-selected: $interhome-red;		// Interhome Rot
$interhome-cal-last-date-selected: $interhome-red;		// Interhome Rot
$interhome-cal-hovering: rgba(230, 57, 87, 0.5);		// Interhome Rot
$interhome-cal-not-available: #fff;						// Weiß
$interhome-cal-real-today: $interhome-blue;				// Interhome Rot
$interhome-cal-link-color: #424242;						// Schwarz
$interhome-cal-link_hover-color: #fff;					// Weiß

// Typography
$font-stack : Arial, 'Helvetica Neue', 'Calibri Light', 'Roboto', sans-serif;


// Colorset / Paragraphs, Links
$body-color : #575c64;									// #5e5e5e
$link-color : #424242;									// Schwarz
$link-color-hover : $interhome-red; 					// Rot
$highlight-color : #de1c8c;								// Magenta
$darkgrey: #232323;
$link-color-site-navigation-primary : $link-color;		// Magenta


// Colorset / Links:hover
$hover-default : #edf3fe;


$accent : #6D87A7;           
$error : #811702;
$warning : #F9E055;
$valid : #038144;


// Colorset / Headings
$heading-color : $interhome-blue;


// Colorset / Preis
$price-color : rgba( 97, 150, 50, 1 );


// Colorset / Border
$border-color : #575c64;												// Schwarz
$light-border-color : #eff3f6;											// Hellgrau
$border-color-light : #eff3f6;											// Hellgrau
$border-color-hover : $interhome-red;									// Interhome-Rot
$outline : $interhome-red;												// Interhome-Rot
$border-radius : 3px;


// Shadows
$box-shadow: 0 0 1.5em rgba(0, 0, 0, 0.12);								// Schatten (s. Treffebox)
$box-shadow-color-default: rgba(204, 204, 204, 1);						// Schattenfarbe
$box-shadow-color: #ccc;
$box-shadow-form-elements : 6px 10px 10px $box-shadow-color;
//  Shadows / Animation
$box-shadow-animation: box-shadow .5s ease;


// Backgrounds
$bg-body-color : #fff;
$bg-page-wrapper : #fff;
$bg-buttons : #f2f2f2;


// Füllfarbe
$fill-color : #f4f7f9;


// Forms
$input-text: $body-color;												// Schwarz
$label-color-hover : $interhome-blue;									// InterhomeBlue
$bg-color-input-focus-active : #e8f9db;					
$bg-color-input-hover : rgba(229, 230, 232, 0.1);						// 20% von InterhomGrey
$bg-color-hover : #e8f9db;												// #e8f9db

// Forms / border-color
$border-color-input : rgba(192, 192, 192, 1);							// #c0c0c0 (silver)
$border-color-textarea : $border-color-input;							// Schwarz
$border-color-input-optional : rgba(147, 128, 108, 0.4);				// #93806c
$border-color-input-hover-focus-active : rgba(97, 150, 50, 1);			
$border-color-input-required : $border-color-input-hover-focus-active;


// Hide/Show
$hide : none;
$show : block;


// Buttons
$btn-bg-color : #fff;
$btn-bg-color-hover : darken($btn-bg-color, 5%);
$btn-bg-color-submit :rgba(97, 150, 50, 1);								// Grün (#61a032) / Hellgrün (#e8f9db)

// Buttons / border
$btn-border-color : #ccc;
$btn-border : 1px solid #ccc;
$btn-border-submit : none;
$btn-border-hover : 1px solid #ccc;


// Spacing
$space-smaller : 5px;
$space-small : 10px;
$space-medium : 20px;
$space-large : 40px;
$space-larger : 80px;
$space-largest : 240px;


// Devices / MediaQueries
$probook: 87.5em; // 1400px
$ultrabook: 81.25em; // 1300px
$bignotebook: 77.5em; // 1240px
$notebook: 75em; // 1200px
$smallnotebook: 71.25em; // 1140px
$tablet-landscape: 68.75em; // 1100px
$protablet-landscape: 62.5em; // 1000px
$phablet-landscape: 59.375em; // 950px
$protablet: 53.125em; // 850px
$tablet: 49em; // 784px
$phablet: 43.125em; // 690px
$smartphone-landscape: 36.25em; // 580px
$smartphone: 29.375em; // 470px
$smartphone-large: 26.25em; // 420px
$smartphone-medium: 23.75em; // 380px
$smartphone-klein: 21.25em; // 340px

$radius: 9px;
$umbruch: 950px;